import { BehaviorSubject } from 'rxjs';
import { Injectable } from '@angular/core';

@Injectable({
    providedIn: 'root'
})
export class AssetDetailService {

    constructor() { }

    idAsset = new BehaviorSubject<string>(null);
    getIdAsset() {
        return this.idAsset.asObservable()
    }

    setIAsset(id_model: string) {
        this.idAsset.next(id_model)
    }

}
