import { Component, OnInit } from '@angular/core';
import { MenuService } from 'src/app/services/menu.service';
import { Router, ActivatedRoute, NavigationStart } from '@angular/router';
import { AssetModelListService, AssetModelxService, AssetPartIdService, AssetLifeLimitService, AssetConditionMonitoringService, VendorVendorService, AssetAssetService, AssetStatusService } from 'src/app/sdk/asset/services';
import { ModelDetailService } from '../model-detail/model-detail.service';
import { Modelx, Part_id, Life_limit, Condition_monitoring, Asset, Status } from 'src/app/sdk/asset/models';
import { filter } from 'rxjs/operators';
import * as _ from 'lodash';
import { AssetDetailService } from './asset-detail.service';

@Component({
    selector: 'app-asset-detail',
    templateUrl: './asset-detail.component.html',
    styleUrls: ['./asset-detail.component.scss']
})
export class AssetDetailComponent implements OnInit {

    menuData: any = []
    isMobileView: boolean
    isMobileMenuOpen: boolean
    isMenuCollapsed: boolean
    isMenuUnfixed: boolean
    isMenuShadow: boolean
    isSidebarOpen: boolean
    menuType: string
    menuColor: string
    flyoutMenuColor: string
    menuLayoutType: string

    activeSubmenu: string = ''
    activeItem: string = ''
    renderedFlyoutItems: object = {}
    flyoutTimers: object = {}
    flyoutActive: boolean = false
    objectKeys = Object.keys

    constructor(
        private menuService: MenuService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private assetModelxService: AssetModelxService,
        private assetAssetService: AssetAssetService,
        private assetStatusService: AssetStatusService,

        private assetPartIdService: AssetPartIdService,
        private assetLifeLimitService: AssetLifeLimitService,
        private assetConditionMonitoringService: AssetConditionMonitoringService,
        private vendorVendorService: VendorVendorService,
        private assetDetailService: AssetDetailService
    ) { }

    id_asset: string = ''
    model: Modelx = {}
    asset: Asset = {}

    ngOnInit() {
        this.activatedRoute.paramMap.subscribe(params => {
            if (params.get('id_asset')) {
                this.id_asset = params.get('id_asset')
                this.assetDetailService.setIAsset(this.id_asset)

                this.getAsset()
            }

        })


        this.menuData = [
            {
                title: 'Dashboard',
                key: 'dashboard',
                icon: 'fe fe-bookmark',
                url: 'dashboard',
            },
            {
                title: 'Detail',
                key: 'datadiri',
                icon: 'fe fe-bookmark',
                url: 'detail',
            },

            {
                title: 'History',
                key: 'Lainnya',
                icon: 'fe fe-home',
                count: 6,
                children: [
                    {
                        title: 'check out',
                        key: 'checkout',
                        icon: 'fe fe-bookmark',
                        url: 'checkout',
                    },
                    {
                        title: 'Complaints',
                        key: 'complaints',
                        icon: 'fe fe-bookmark',
                        url: 'complaints',
                    }

                ],
            },
            {
                category: true,
                title: 'lain lain',
            },
            {
                title: 'Spesification',
                key: 'pengaturan-absensi',
                icon: 'fe fe-bookmark',
                url: 'spesifikasi',
            },
            {
                title: 'Perbaikan',
                key: 'dipecat',
                icon: 'fe fe-bookmark',
                url: '/hr/direktori/module/dipecat',
            },
            {
                title: 'Component',
                key: 'component',
                icon: 'fe fe-bookmark',
                url: 'component',
            },
            {
                title: 'Consumable',
                key: 'consumsiable',
                icon: 'fe fe-bookmark',
                url: 'consumsiable',
            },
            {
                title: 'License',
                key: 'license',
                icon: 'fe fe-bookmark',
                url: 'license',
            }


        ]

        // this.setActiveItems(this.router.url)
        this.router.events
            .pipe(filter(event => event instanceof NavigationStart))
            .subscribe((event: NavigationStart) => {
                // this.setActiveItems(event.url ? event.url : null)
            })
    }

    getAsset() {
        this.assetAssetService.getAssetAssetsId(this.id_asset).subscribe(
            data => {
                this.asset = data.data;
                this.getModel(this.asset.id_model)
                this.getStatus(this.asset.id_status)
            }
        )
    }

    getModel(id_model) {
        this.assetModelxService.getAssetModelxesId(id_model).subscribe(
            data => {
                this.model = data.data;
                this.getPartId(this.model.id_part_id)

            }
        )
    }

    partId: Part_id = {};
    getPartId(id) {
        this.assetPartIdService.getAssetPartIdsId(id).subscribe(
            data => {
                this.partId = data.data
            }
        )
    }

    status: Status = {};
    getStatus(id) {
        this.assetStatusService.getAssetStatusesId(id).subscribe(
            data => {
                this.status = data.data
            }
        )
    }



    toggleMobileMenu() { }

    toggleMenu() { }

    toggleSettings() { }

    handleSubmenuClick(key: string) {
        const currentKey = this.activeSubmenu
        if (this.flyoutActive) {
            return
        }
        this.activeSubmenu = currentKey === key ? '' : key
    }

    setActiveItems(pathname) {
        const menuData = this.menuData
        const flattenItems = (items, key) =>
            items.reduce((flattenedItems, item) => {
                flattenedItems.push(item)
                if (Array.isArray(item[key])) {
                    return flattenedItems.concat(flattenItems(item[key], key))
                }
                return flattenedItems
            }, [])
        const activeItem = _.find(flattenItems(menuData, 'children'), ['url', pathname])
        const activeSubmenu = menuData.reduce((key, parent) => {
            if (Array.isArray(parent.children)) {
                parent.children.map(child => {
                    if (child.key === activeItem.key) {
                        key = parent
                    }
                    return ''
                })
            }
            return key
        })

        this.activeItem = activeItem.key
        this.activeSubmenu = activeSubmenu.key
    }

    handleFlyoutOver(event, key, items) {
        if (this.flyoutActive) {
            clearInterval(this.flyoutTimers[key])
            const item = event.currentTarget
            const itemDimensions = item.getBoundingClientRect()
            this.renderedFlyoutItems = {
                ...this.renderedFlyoutItems,
                [key]: {
                    key,
                    itemDimensions,
                    items,
                },
            }
        }
    }

    handleFlyoutOut(key) {
        if (this.flyoutActive) {
            this.flyoutTimers[key] = setTimeout(() => {
                const updatedFlyoutItems = Object.assign({}, this.renderedFlyoutItems)
                delete updatedFlyoutItems[key]
                this.renderedFlyoutItems = {
                    ...updatedFlyoutItems,
                }
            }, 100)
        }
    }

    handleFlyoutContainerOver(key) {
        clearInterval(this.flyoutTimers[key])
    }


}
