import { ComplaintsComponent } from './riwayat/complaints/complaints.component';
import { CheckOutComponent } from './riwayat/check-out/check-out.component';
import { LicenseComponent } from './license/license.component';
import { ConsumsiableComponent } from './consumsiable/consumsiable.component';
import { ComponentComponent } from './component/component.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AssetDetailComponent } from './asset-detail.component';
import { DetailComponent } from './detail/detail.component';
import { SpesifikasiComponent } from './spesifikasi/spesifikasi.component';


const routes: Routes = [
    {
        path: '',
        component: AssetDetailComponent,
        children: [
            {
                path: '',
                redirectTo: 'detail'
            },
            {
                path: 'detail',
                component: DetailComponent
            },
            {
                path: 'spesifikasi',
                component: SpesifikasiComponent
            },
            {
                path: 'component',
                component: ComponentComponent
            },
            {
                path: 'consumsiable',
                component: ConsumsiableComponent
            },
            {
                path: 'license',
                component: LicenseComponent
            },
            {
                path: 'checkout',
                component: CheckOutComponent
            },
            {
                path: 'complaints',
                component: ComplaintsComponent
            },
        ]
    }

];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class AssetDetailRoutingModule { }
