import { SearchItemComponent } from 'src/app/components/search-item/search-item/search-item.component';
import { AssetDetailService } from './../asset-detail.service';
import { Component, OnInit } from '@angular/core';
import { AssetModelTemplateChildService, AssetAssetTemplateChildService, AssetAssetService } from 'src/app/sdk/asset/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService, NzDrawerService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router } from '@angular/router';
import { Model_template_child, Asset_template_child } from 'src/app/sdk/asset/models';
import { SearchAssetComponent } from 'src/app/components/search-item/search-asset/search-asset.component';

@Component({
    selector: 'app-component',
    templateUrl: './component.component.html',
    styleUrls: ['./component.component.scss']
})
export class ComponentComponent implements OnInit {

    constructor(
        private assetAssetTemplateChildService: AssetAssetTemplateChildService,
        private assetDetailService: AssetDetailService,
        private assetAssetService: AssetAssetService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private router: Router,
        private drawerService: NzDrawerService
    ) { }


    listAssetChild: Asset_template_child[] = []
    id_asset: string = ''
    id_parent: string = null

    id_part_id: string = 'componen'

    ngOnInit() {
        this.assetDetailService.getIdAsset().subscribe(
            data => {
                if (data) {
                    this.id_asset = data
                    this.getlistChild()
                }
            }
        )

    }

    add_item() {
        const drawerRef = this.drawerService.create<SearchAssetComponent, { value: string }, string>({
            nzTitle: 'Search Asset',
            nzContent: SearchAssetComponent,
            nzWidth: '900px',
            nzContentParams: {
                value: 'arifan'
            }
        });

        drawerRef.afterOpen.subscribe(() => {
            console.log('Drawer(Component) open');
        });

        drawerRef.afterClose.subscribe(data => {

            if (typeof data === 'string') {
                this.id_parent = data;
                this.beforeModel()
            }
        });
    }


    getlistChild() {
        let param = {
            id_parent: this.id_asset,
            id_part_id: this.id_part_id
        }
        this.assetAssetTemplateChildService.getAssetAssetTemplateChildren(JSON.stringify(param)).subscribe(
            data => {
                this.listAssetChild = data.data
            }
        )
    }


    deleteItem(id) {
        this.spinner.show()
        this.assetAssetTemplateChildService.deleteAssetAssetTemplateChildrenId(id).subscribe(
            data => {
                this.getlistChild()
                this.spinner.hide()
            }
        )
    }


    dataItem: any = {};


    beforeModel() {
        console.log("panggie ko")
        this.spinner.show()
        this.assetAssetService.getAssetAssetsId(this.id_parent).subscribe(
            data => {
                this.dataItem.sn = data.data.sn;
                this.dataItem.qty = data.data.qty;

                this.spinner.hide()


                console.log(this.dataItem)

            }
        )
    }

    qty_asset: number
    simpan_item() {

        let param: Asset_template_child = {
            id_parent: this.id_asset,
            id_asset: this.id_parent,
            qty_asset: this.qty_asset
        };

        this.spinner.show()
        //simpan ka server
        this.assetAssetTemplateChildService.postAssetAssetTemplateChildren(param).subscribe(
            data => {
                this.id_parent = null;
                this.spinner.hide();
                this.getlistChild()
            },
            err => {
                this.spinner.hide();
                this.pesan.pesanWarningForm('Dupikat pembelian Item!')
            }
        )
    }
}
