import { Component, OnInit } from '@angular/core';
import { AssetCheckoutService } from 'src/app/sdk/asset/services';
import { NzMessageService } from 'ng-zorro-antd';
import { NgxSpinnerService } from 'ngx-spinner';
import { AssetDetailService } from '../../asset-detail.service';

@Component({
  selector: 'app-check-out',
  templateUrl: './check-out.component.html',
  styleUrls: ['./check-out.component.scss']
})
export class CheckOutComponent implements OnInit {

    constructor(
        private assetCheckoutService: AssetCheckoutService,
        private assetDetailService: AssetDetailService,
        private nzMessageService: NzMessageService,
        private spinner: NgxSpinnerService
    ) { }

    id_asset: string = ''
    ngOnInit() {
        this.assetDetailService.getIdAsset().subscribe(
            data=> {
                this.id_asset = data
            }
        )
        this.searchData()
    }

    pageIndex = 1;
    pageSize = 10;
    total = 1;
    listOfData = [];
    loading = true;
    sortValue: string | null = null;
    sortKey: string | null = null;
    filterGender = [{ text: 'male', value: 'male' }, { text: 'female', value: 'female' }];
    search: string = '';
    sort(sort: { key: string; value: string }): void {
        this.sortKey = sort.key;
        this.sortValue = sort.value;
        this.searchData();
    }

    searchData(reset: boolean = false): void {
        if (reset) {
            this.pageIndex = 1;
        }
        this.loading = true;
        this.assetCheckoutService
            .getAssetCheckouts(JSON.stringify({
                pageIndex: this.pageIndex, pageSize: this.pageSize, sortKey: this.sortKey!, sortValue: this.sortValue!, search: this.search, id_asset:this.id_asset
            }))
            .subscribe((data: any) => {
                this.loading = false;
                this.total = data.data.total;
                this.listOfData = data.data.data;
            });
    }

    updateFilter(value: string): void {
        this.search = value;
        this.searchData(true);
    }


}
