import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { ModelDetailService } from '../../model-detail/model-detail.service';
import { AssetAssetService, AssetAssetSpecificationService } from 'src/app/sdk/asset/services';
import { AssetDetailService } from '../asset-detail.service';
import { Asset_specification } from 'src/app/sdk/asset/models';

@Component({
    selector: 'app-spesifikasi',
    templateUrl: './spesifikasi.component.html',
    styleUrls: ['./spesifikasi.component.scss']
})
export class SpesifikasiComponent implements OnInit {

    constructor(
        private assetAssetService: AssetAssetService,
        private assetAssetSpecificationService: AssetAssetSpecificationService,
        private modelDetailService: ModelDetailService,

        private router: Router,
        private activatedRoute: ActivatedRoute,
        private spinner: NgxSpinnerService,
        private pesan: PesanService,
        private assetDetailService: AssetDetailService

    ) { }

    id_model: string = ''
    id_asset: string = ''
    ngOnInit() {
        this.assetDetailService.getIdAsset().subscribe(
            data => {
                if (data) {
                    this.id_asset = data
                    this.getAsset()
                }
            }
        )
    }

    listSpesifikasi: Asset_specification[] = []
    getAsset() {
        this.assetAssetSpecificationService.getAssetAssetSpecifications(JSON.stringify({ id_asset: this.id_asset })).subscribe(
            data => {
                this.listSpesifikasi = data.data

                data.data.forEach((item, index) => {
                    if ('select' == item['field_type']) {
                        this.listSpesifikasi[index]['option_value'] = JSON.parse(item['option_value']);
                        console.log(this.listSpesifikasi[index]['option_value'] )
                    }
                })

            }
        )
    }

    edit(id_model_specification, id_asset_specification) {
        this.listSpesifikasi.forEach((item, index) => {
            this.listSpesifikasi[index]['edit'] = false;
            if (id_model_specification == item.id_model_specification) {
                this.listSpesifikasi[index]['edit'] = true;
            }
        })
    }

    SimpanUpdate(id_model_specification, id_asset_specification,value_spesification) {


        this.spinner.show()
        let params: Asset_specification = {
            id_asset_specification: id_asset_specification,
            id_model_specification: id_model_specification,
            id_asset: this.id_asset,
            value_spesification: value_spesification
        }

        this.assetAssetSpecificationService.putAssetAssetSpecificationsId({id:'1', body: params}).subscribe(
            data => {
                this.spinner.hide()

                this.listSpesifikasi.forEach((item, index) => {
                    if (id_model_specification == item.id_model_specification) {
                        this.listSpesifikasi[index]['edit'] = false;

                    }
                })
            },
            err => {
                this.spinner.hide()
                this.pesan.pesanWarningForm('Data tidak valid')
            }
        )

    }


}
