import { Condition_monitoring } from 'src/app/sdk/asset/models/condition-_monitoring';
import { AssetAssetService, AssetPartIdService, AssetLifeLimitService, VendorVendorService, AssetModelxService, AssetConditionMonitoringService, AssetBinLocationService, AssetStatusService, VendorManufactureService } from 'src/app/sdk/asset/services';
import { AssetDetailService } from './../asset-detail.service';
import { Component, OnInit } from '@angular/core';
import { Status, Asset, Part_id, Vendor, Modelx, Manufacture, Bin_location, Life_limit } from 'src/app/sdk/asset/models';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
    selector: 'app-detail',
    templateUrl: './detail.component.html',
    styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private assetPartIdService: AssetPartIdService,
        private assetAssetService: AssetAssetService,
        private assetLifeLimitService: AssetLifeLimitService,
        private vendorVendorService: VendorVendorService,
        private assetModelxService: AssetModelxService,
        private assetBinLocationService: AssetBinLocationService,
        private assetStatusService: AssetStatusService,
        private vendorManufactureService: VendorManufactureService,
        private assetConditionMonitoringService: AssetConditionMonitoringService,

        private assetDetailService: AssetDetailService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
    ) { }

    id_asset: string = ''
    dataModel: Modelx = {}
    dataStatus: Status = {}
    dataAsset: Asset = {}
    dataType: Part_id = {}
    dataVendor: Vendor = {}
    dataManufacture: Manufacture = {}
    dataBin: Bin_location = {}
    dataLifelimit: Life_limit = {};
    dataCondition: Condition_monitoring = {};


    ngOnInit() {
        this.id_asset = this.activatedRoute.snapshot.paramMap.get('id_asset')
        this.assetDetailService.getIdAsset().subscribe(
            data => {
                if (data) {
                    this.id_asset = data
                    this.getAsset()
                }
            }
        )

    }


    getAsset() {
        this.assetAssetService.getAssetAssetsId(this.id_asset).subscribe(
            data => {
                this.dataAsset = data.data;
                this.getBin(this.dataAsset.id_bin_location)
                this.getStatus(this.dataAsset.id_status)
                this.getVendor(this.dataAsset.id_vendor)
                this.getModel(this.dataAsset.id_model)

            }
        )
    }


    getModel(id_model) {
        this.assetModelxService.getAssetModelxesId(id_model).subscribe(
            data => {
                this.dataModel = data.data
                this.getPartId(this.dataModel.id_part_id)
                this.getConditionMonitoring(this.dataModel.id_condition_monitoring)
                this.getLifeLimit(this.dataModel.id_life_limit)
            }
        )
    }
    getPartId(id_part_id) {
        this.assetPartIdService.getAssetPartIdsId(id_part_id).subscribe(
            data => {
                this.dataType = data.data
            }
        )
    }
    getLifeLimit(id_life_limit) {
        this.assetLifeLimitService.getAssetLifeLimitsId(id_life_limit).subscribe(
            data => {
                this.dataLifelimit = data.data
            }
        )
    }
    getConditionMonitoring(id_condition_monitoring) {
        this.assetConditionMonitoringService.getAssetConditionMonitoringsId(id_condition_monitoring).subscribe(
            data => {
                this.dataCondition = data.data
            }
        )
    }
    getManufacture(id_manufacture) {
        this.vendorManufactureService.getVendorManufacturesId(id_manufacture).subscribe(
            data => {
                this.dataManufacture = data.data
            }
        )
    }

    getBin(id_bin_location) {
        this.assetBinLocationService.getAssetBinLocationsId(id_bin_location).subscribe(
            data => {
                this.dataBin = data.data
            }
        )
    }
    getStatus(id_status) {
        this.assetStatusService.getAssetStatusesId(id_status).subscribe(
            data => {
                this.dataStatus = data.data
            }
        )
    }
    getVendor(id_vendor) {
        this.vendorVendorService.getVendorVendorsId(id_vendor).subscribe(
            data => {
                this.dataVendor = data.data
            }
        )
    }


}
